@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  font-family: "Open Sans", serif;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background: rgba(45,176,224,1);
  background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(45,176,224,1)), color-stop(100%, rgba(96,40,168,1)));
  background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2db0e0', endColorstr='#6028a8', GradientType=1 );
}

.main {
  background: #2a6bb1;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
  margin: 0 auto;
  border-radius:10px;
}

.info {
  background: url(/weather-cities/static/media/polygon.cc54c19b.jpg) center center no-repeat;
  background-blend-mode: multiply;
  background-color: #484747;
  border: 2px solid #1f1f1f;
  border-right: 2px solid #131e29;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.form {
  min-height: 400px;
  padding-top: 100px;
  padding-left: 50px;
  border-top: 2px solid #113a65;
  border-bottom: 2px solid #113a65;
  border-right: 2px solid #113a65;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #113a65;
  width: 50%;
  padding-bottom: 4px;
  color: #fff;
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
}

input::-webkit-input-placeholder {
  color: #103a65;
}

input::-moz-placeholder {
  color: #103a65;
}

button {
  border: 2px solid #103a65;
  border-bottom: 4px solid #103a65;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  cursor: pointer;
  background-color: #235d9c;
  color: #fff;
  outline: none!important;
}

button:hover {
  border-bottom: 2px solid #103a65;
  position: relative;
  top: 2px;
}

button:active {
  border-bottom: 2px solid transparent;
  border-color: transparent;
  position: relative;
  top: 2px;
}

.info h2 {
  font-size: 3em;
  color: #fff;
}

.info p {
  color: #e4e4e4;
  font-weight: bold;
}

.infoWeath {
  width: 85%;
  font-size: 15px;
  color: #fff;
}

.error {
  color: #041f3c;
  font-weight: bold;
}

@media screen and (max-width: 575px) {
  
  .info {
    border-radius: 0;
    min-height: 300px;
  }

  .form {
    border-radius: 0;
    padding: 10px;
    text-align: center;
    font-size: 20px;
  }

  .form  form {
    min-height: 150px;
    display: flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    margin-bottom: 20px;
  }

  .form form input[type="text"] {
    width: 90%;
  }
}

@media screen and (max-width: 767px) {
   .info {
     min-height: 400px;
     border: 2px solid #1f1f1f;
     border-top-left-radius:10px;
     border-top-right-radius: 10px;
     border-bottom-left-radius: 0;
   }

   .form {
     border: 2px solid #113a65;
     border-top-right-radius: 0;
   }

   .infoWeath {
     width: 100%;
   }

}
